body {
    background: rgb(20, 220, 153) ;
    display: flex;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
}

.form-container {
   width: 360px;
   background-color: white;
   margin: auto;
   margin: 5px;
   box-shadow: 10px 10px 5px rgba(0,0,0,0.5);
   padding: 10px;
}
.register-from {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 10px;
    
} 
.from-field {
    margin: 10px 0 10px 0;
    padding: 15px;
    font-size: 16px;
    border: 0;
    font-family:"Roboto", sans-serif;
    box-shadow: 10px 10px 5px rgba(0,0,0,0.5);
}
.success-message {
    margin: 5px;
    padding: 10px;
    height: 3rem;
    text-align: center;
    align-items: center;

}
span {
    font-family: "Roboto", sans-serif;
    font-size: 15px;
    color: red;
    margin-bottom: 15px;

}
input {
    margin: 5px;
    height: 3rem;
    background: #f2f2f2;
}

.error {
    border-style: solid;
    border: 2px solid #ffa4a4;
}

button {
    background: darkcyan;
    color: rgb(255, 255, 255);
    cursor: pointer;
}

button:disabled {
    cursor: default;
}